import Api from '@/utils/axios/Api';
import {inbox , msg , msgRequest} from "@/models/chat"

const Http = Api.getInstance();







export const ListAllChats = () :Promise<inbox[]> => {
    return new Promise((resolve, reject) => {
        Http.get('chat')
        .then((d) => {
            resolve(d)
        }).catch((e:any) => {
            reject(e)
        })
    })
}


export const ListChatMsgs = (id:number) :Promise<msg[]> => {
    return new Promise((resolve, reject) => {
        Http.get(`chat/${id}`)
        .then((d) => {
            resolve(d)
        }).catch((e:any) => {
            reject(e)
        })
    })
}


export const CreateMsg = (payload:msgRequest) :Promise<number> => {
    return new Promise((resolve, reject) => {
        Http.post(`chat` , payload)
        .then((d) => {
            resolve(d)
        }).catch((e:any) => {
            reject(e)
        })
    })
}

